header {
  position: relative;
}
header .bg-desktop {
  display: none;
}
@media (min-width: 768px) {
  header .bg-desktop {
    display: flex;
  }
}
header .container__steps {
  display: flex;
  justify-content: center;
  gap: 1.8rem;
  margin: 3.2rem 0;
}
@media (min-width: 768px) {
  header .container__steps {
    margin: 0;
    flex-direction: column;
    position: absolute;
    top: 0;
    padding: 3rem;
    gap: 3rem;
  }
}
header .container__steps > div {
  display: flex;
  gap: 2rem;
}
header .container__steps > div > .number {
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  border: 1px solid hsl(0deg, 0%, 100%);
  color: hsl(0deg, 0%, 100%);
  font-size: 16px;
  font-weight: 500;
  display: grid;
  place-content: center;
}
header .container__steps .container__data-step {
  display: none;
  gap: 2rem;
  font-size: 16px;
}
@media (min-width: 768px) {
  header .container__steps .container__data-step {
    display: block;
  }
}
header .container__steps .container__data-step .number-step {
  text-transform: uppercase;
  color: hsl(231deg, 11%, 63%);
  font-weight: 700;
}
header .container__steps .container__data-step .name-step {
  text-transform: uppercase;
  color: hsl(231deg, 100%, 99%);
}

.content, .content__select-plan, .content__personal-info {
  background-color: hsl(231deg, 100%, 99%);
  display: flex;
  flex-direction: column;
  padding: 2.5rem;
  margin: 0 1.5rem;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .content, .content__select-plan, .content__personal-info {
    margin: 0;
    justify-content: center;
    padding-top: 3rem;
  }
}
.content > h2, .content__select-plan > h2, .content__personal-info > h2 {
  color: hsl(213deg, 96%, 18%);
  font-size: 2.45rem;
  margin: 0;
}
@media (min-width: 768px) {
  .content > h2, .content__select-plan > h2, .content__personal-info > h2 {
    font-size: 3.5rem;
    margin-top: 1rem;
  }
}
.content > p, .content__select-plan > p, .content__personal-info > p {
  font-size: 16px;
  color: hsl(231deg, 11%, 63%);
  margin-bottom: 2rem;
  line-height: 1.5;
  font-weight: 500;
}
@media (min-width: 768px) {
  .content > p, .content__select-plan > p, .content__personal-info > p {
    font-size: 1.6rem;
    margin: 0.8rem 0 2.5rem 0;
  }
}

@media (min-width: 768px) {
  .container__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-right: 6.5rem;
  }
}

.content__personal-info {
  display: none;
}
.content__personal-info .container__form {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
  gap: 1.5rem;
}
.content__personal-info .container__form .campo {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .content__personal-info .container__form .campo {
    gap: 1rem;
  }
}
.content__personal-info .container__form .campo > label {
  display: flex;
  justify-content: space-between;
  font-size: 1.3rem;
  color: hsl(213deg, 96%, 18%);
}
@media (min-width: 768px) {
  .content__personal-info .container__form .campo > label {
    font-size: 1.6rem;
    font-weight: 500;
  }
}
.content__personal-info .container__form .campo > input {
  padding: 0.9rem;
  font-size: 1.5rem;
  font-weight: 500;
  border: 1px solid hsl(229deg, 24%, 87%);
  border-radius: 8px;
}
@media (min-width: 768px) {
  .content__personal-info .container__form .campo > input {
    padding: 1.3rem;
  }
}
.content__personal-info .container__form .campo > input::placeholder {
  color: hsl(231deg, 11%, 63%);
}
.content__personal-info .container__form .campo > input:focus {
  border: 1px solid hsl(243deg, 100%, 62%);
}

.content__select-plan {
  display: none;
}
.content__select-plan .container__plans {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
@media (min-width: 768px) {
  .content__select-plan .container__plans {
    width: 100%;
    flex-direction: row;
  }
}
.content__select-plan .container__plans .plan {
  display: flex;
  border: 1px solid hsl(229deg, 24%, 87%);
  border-radius: 8px;
  padding: 1.4rem;
  gap: 1.5rem;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .content__select-plan .container__plans .plan {
    flex-direction: column;
    gap: 4rem;
    flex: 1;
  }
  .content__select-plan .container__plans .plan:hover {
    border: 1px solid hsl(243deg, 100%, 62%);
  }
  .content__select-plan .container__plans .plan:focus {
    background-color: hsl(217deg, 100%, 97%);
  }
}
.content__select-plan .container__plans .plan .data {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
}
.content__select-plan .container__plans .plan .data > h3 {
  font-size: 1.7rem;
  color: hsl(213deg, 96%, 18%);
  margin: 0;
}
.content__select-plan .container__plans .plan .data > h4 {
  font-size: 1.5rem;
  font-weight: 400;
  color: hsl(231deg, 11%, 63%);
  margin: 0;
}
.content__select-plan .container__plans .plan .data-year {
  display: none;
  gap: 1rem;
}
.content__select-plan .container__plans .plan:focus {
  border: 1px solid hsl(243deg, 100%, 62%);
}

.time {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  margin: 4rem 0;
}
@media (min-width: 768px) {
  .time {
    background-color: hsl(217deg, 100%, 97%);
    border-radius: 8px;
  }
}
.time > h3 {
  font-size: 1.5rem;
  color: hsl(229deg, 24%, 87%);
}

.y {
  display: none;
}

.container__switch .lbl {
  display: inline-block;
  width: 30px;
  height: 16px;
  background-color: hsl(213deg, 96%, 18%);
  border-radius: 100px;
  cursor: pointer;
  position: relative;
}
.container__switch .lbl::after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  background: hsl(217deg, 100%, 97%);
  border-radius: 50%;
  position: absolute;
  top: 3px;
  left: 3px;
  transition: left 0.2s linear;
}
.container__switch #switch {
  display: none;
}
.container__switch #switch:checked + .lbl::after {
  left: 17px;
}

footer {
  background-color: hsl(231deg, 100%, 99%);
  margin-top: 14rem;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  footer {
    background-color: transparent;
    margin: 0;
  }
}
footer .btn {
  border-radius: 5px;
  border: none;
  margin: 1.5rem;
  padding: 1rem 1.8rem;
  font-size: 16px;
  cursor: pointer;
}
footer .back-step {
  color: hsl(231deg, 11%, 63%);
  background-color: transparent;
  font-weight: 700;
}
@media (min-width: 768px) {
  footer .back-step:hover {
    color: hsl(213deg, 96%, 18%);
    font-weight: 700;
  }
}
footer .next-step {
  color: hsl(217deg, 100%, 97%);
  background-color: hsl(213deg, 96%, 18%);
}

.content, .content__thank-you, .content__finishing-up, .content__pick-addons {
  background-color: hsl(231deg, 100%, 99%);
  display: flex;
  flex-direction: column;
  padding: 2.5rem;
  margin: 0 1.5rem;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .content, .content__thank-you, .content__finishing-up, .content__pick-addons {
    margin: 0;
    justify-content: center;
    padding-top: 3rem;
  }
}
.content > h2, .content__thank-you > h2, .content__finishing-up > h2, .content__pick-addons > h2 {
  color: hsl(213deg, 96%, 18%);
  font-size: 2.45rem;
  margin: 0;
}
@media (min-width: 768px) {
  .content > h2, .content__thank-you > h2, .content__finishing-up > h2, .content__pick-addons > h2 {
    font-size: 3.5rem;
    margin-top: 1rem;
  }
}
.content > p, .content__thank-you > p, .content__finishing-up > p, .content__pick-addons > p {
  font-size: 16px;
  color: hsl(231deg, 11%, 63%);
  margin-bottom: 2rem;
  line-height: 1.5;
  font-weight: 500;
}
@media (min-width: 768px) {
  .content > p, .content__thank-you > p, .content__finishing-up > p, .content__pick-addons > p {
    font-size: 1.6rem;
    margin: 0.8rem 0 2.5rem 0;
  }
}

@media (min-width: 768px) {
  .container__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-right: 6.5rem;
  }
}

.content__pick-addons {
  display: none;
}
.content__pick-addons .container__addons {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.content__pick-addons .container__addons .addon {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid hsl(229deg, 24%, 87%);
  border-radius: 10px;
  padding: 0.8rem 1.5rem;
  cursor: pointer;
}
.content__pick-addons .container__addons .addon .container__checkbox > input[type=checkbox] {
  display: none;
}
.content__pick-addons .container__addons .addon .container__checkbox > input[type=checkbox]:checked + img {
  background-color: hsl(243deg, 100%, 62%);
  padding: 0.5rem 0.4rem;
  border-radius: 4.5px;
  border: 1px solid hsl(243deg, 100%, 62%);
}
.content__pick-addons .container__addons .addon .container__checkbox > img {
  border: 1px solid hsl(229deg, 24%, 87%);
  padding: 0.5rem 0.4rem;
  border-radius: 4.5px;
}
.content__pick-addons .container__addons .addon .addon-info {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding-left: 1rem;
  flex: 1;
}
.content__pick-addons .container__addons .addon .addon-info h3 {
  font-size: 1.5rem;
  margin: 0;
  color: red;
}
.content__pick-addons .container__addons .addon .addon-info p {
  font-size: 1.25rem;
  margin: 0;
}
.content__pick-addons .container__addons .addon .addon-year {
  display: none;
}
.content__pick-addons .container__addons .addon > h4 {
  font-size: 1.3rem;
  color: hsl(243deg, 100%, 62%);
  font-weight: 500;
}

.content__finishing-up {
  display: none;
}

.container__results {
  width: calc(100% + 5rem);
}

#container__appResults {
  width: 100%;
  background-color: hsl(217deg, 100%, 97%);
  margin-top: 2rem;
  padding: 2rem;
}
#container__appResults:last-child {
  margin-bottom: 4rem;
}
#container__appResults .separator {
  margin: 2rem 0;
  width: 100%;
  background-color: hsl(229deg, 24%, 87%);
  height: 1px;
}

.finish-plan {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.finish-plan > div > h3 {
  font-size: 1.6rem;
  margin: 0;
  margin-bottom: 0.6rem;
}
.finish-plan > div > a {
  font-size: 1.4rem;
  color: hsl(231deg, 11%, 63%);
  text-decoration: underline;
}
.finish-plan > h3 {
  font-size: 1.7rem;
}

.finish-addon {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}
.finish-addon .name-addon {
  color: hsl(231deg, 11%, 63%);
  margin: 0;
}
.finish-addon .price-addon {
  margin: 0;
}

.content__thank-you {
  text-align: center;
  display: none;
  padding-bottom: 5rem;
}
.content__thank-you > img {
  width: 6rem;
  margin: 5rem auto 2rem auto;
}